import { useEffect } from 'react';
import { TransformWrapper, TransformComponent } from 'react-zoom-pan-pinch';
import styles from './imageModal.module.scss';
import { Svgs } from '../../assets';

type Props = {
  photos: string[];
  index: number;
  onClose: () => void;
  onPrev: () => void;
  onNext: () => void;
};

export function ImageModal({ photos, index, onClose, onPrev, onNext }: Props) {
  const src = photos[index];

  useEffect(() => {
    const onKey = (e: KeyboardEvent) => {
      if (e.key === 'Escape') onClose();
      if (e.key === 'ArrowLeft') onPrev();
      if (e.key === 'ArrowRight') onNext();
    };
    window.addEventListener('keydown', onKey);
    return () => window.removeEventListener('keydown', onKey);
  }, [onClose, onPrev, onNext]);

  return (
    <div
      className={styles.modalOverlay}
      onClick={onClose}
      role="dialog"
      aria-modal="true"
    >
      <div className={styles.modal} onClick={(e) => e.stopPropagation()}>
        <div className={styles.modalBody}>
          <TransformWrapper
            doubleClick={{ mode: 'toggle' }}
            pinch={{ step: 5 }}
            wheel={{ disabled: true }}
            panning={{ velocityDisabled: true }}
          >
            <TransformComponent>
              <img
                className={styles.modalImg}
                src={src}
                alt={`  ${index + 1}`}
              />
            </TransformComponent>
          </TransformWrapper>
        </div>

        <button
          className={[styles.iconBtn, styles.prev].join(' ')}
          onClick={onPrev}
          aria-label=" "
        >
          <Svgs.arrow fill="#ffffff" />
        </button>

        <button
          className={[styles.iconBtn, styles.next].join(' ')}
          onClick={onNext}
          aria-label=" "
        >
          <Svgs.arrow fill="#ffffff" />
        </button>
        <div className={styles.modalIndex}>
          {index + 1} / {photos.length}
        </div>
        <button className={styles.closeBtn} onClick={onClose} aria-label="">
          <Svgs.close />
        </button>
      </div>
    </div>
  );
}
